<template>
  <div>
    <div class="title">xxx监测报告2</div>
    <el-form :model="reportForm" ref="reportForm" label-width="150px">
      <!-- 机构基本信息 -->
      <label style="margin-left: 30px">机构基本信息</label>
      <el-form-item label="机构名称" prop="companyBasicInfo.companyName" :rules="[{ required: true, message: '不能为空' }]">
        <el-input size="mini" v-model="reportForm.companyBasicInfo.companyName"></el-input>
      </el-form-item>
      <el-form-item label="机构代码" prop="companyBasicInfo.companyCode">
        <el-input size="mini" v-model="reportForm.companyBasicInfo.companyCode"></el-input>
      </el-form-item>
      <el-form-item label="法人" prop="companyBasicInfo.legalPersonName">
        <el-input size="mini" v-model="reportForm.companyBasicInfo.legalPersonName"></el-input>
      </el-form-item>
      <el-form-item label="联系人" prop="companyBasicInfo.contactPerson">
        <el-input size="mini" v-model="reportForm.companyBasicInfo.contactPerson"></el-input>
      </el-form-item>
      <el-form-item label="联系电话" prop="companyBasicInfo.phone">
        <el-input size="mini" v-model="reportForm.companyBasicInfo.phone"></el-input>
      </el-form-item>
      <el-form-item label="单位地址" prop="companyBasicInfo.adress">
        <el-input size="mini" v-model="reportForm.companyBasicInfo.adress"></el-input>
      </el-form-item>
      <el-form-item label="邮政编码" prop="companyBasicInfo.postcode">
        <el-input size="mini" v-model="reportForm.companyBasicInfo.postcode"></el-input>
      </el-form-item>
      <el-form-item label="机构类别" prop="companyBasicInfo.companyType">
        <el-input size="mini" v-model="reportForm.companyBasicInfo.companyType"></el-input>
      </el-form-item>
      <!-- 报备信息 -->
      <label style="margin-left: 30px">报备信息</label>
      <el-form-item label="业务统一代码" prop="reportInfo.businessCode">
        <el-input size="mini" v-model="reportForm.reportInfo.businessCode"></el-input>
      </el-form-item>
      <el-form-item label="任务名称" prop="reportInfo.projectName">
        <el-input size="mini" v-model="reportForm.reportInfo.projectName"></el-input>
      </el-form-item>
      <el-form-item label="业务编号" prop="reportInfo.businessNumber">
        <el-input size="mini" v-model="reportForm.reportInfo.businessNumber"></el-input>
      </el-form-item>
      <el-form-item label="业务类型" prop="reportInfo.businessType">
        <el-input size="mini" v-model="reportForm.reportInfo.businessType"></el-input>
      </el-form-item>
      <el-form-item label="受测单位" prop="reportInfo.particiCompany">
        <el-input size="mini" v-model="reportForm.reportInfo.particiCompany"></el-input>
      </el-form-item>
      <el-form-item label="委托单位" prop="reportInfo.entrastCompany">
        <el-input size="mini" v-model="reportForm.reportInfo.entrastCompany"></el-input>
      </el-form-item>
      <el-form-item label="联系人" prop="reportInfo.contactPerson">
        <el-input size="mini" v-model="reportForm.reportInfo.contactPerson"></el-input>
      </el-form-item>
      <el-form-item label="联系方式" prop="reportInfo.phone">
        <el-input size="mini" v-model="reportForm.reportInfo.phone"></el-input>
      </el-form-item>
      <!-- 采样信息 -->
      <label style="margin-left: 30px">采样信息</label>
      <el-form-item label="监测项目" prop="samplingInfo.monitorProject" :rules="[{ required: true, message: '不能为空' }]">
        <el-input size="mini" v-model="reportForm.samplingInfo.monitorProject"></el-input>
      </el-form-item>
      <el-form-item label="时间" prop="samplingInfo.duration">
        <el-input size="mini" v-model="reportForm.samplingInfo.duration"></el-input>
      </el-form-item>
      <el-form-item label="采样人员" prop="samplingInfo.samplingPerson">
        <el-input size="mini" v-model="reportForm.samplingInfo.samplingPerson"></el-input>
      </el-form-item>
      <el-form-item label="上传图片">
        <el-upload ref="upload" action="no" multiple :http-request="uploadFileConfig" :on-change="addFile" :on-remove="removeFile" :limit="1" :auto-upload="false">
          <el-button slot="trigger" type="warning" size="mini" style="margin-bottom: 10px">选择图片</el-button>
          <span slot="tip" class="el-upload__tip">&nbsp;只能上传jpg/png文件，且不超过10MB</span>
        </el-upload>
      </el-form-item>
      <el-form-item label="备注" prop="samplingInfo.remake">
        <el-input size="mini" type="textarea" :rows="7" style="width: 1000px" v-model="reportForm.samplingInfo.remake"></el-input>
      </el-form-item>
      <el-form-item style="width: 1000px; text-align: center; padding: 20px">
        <el-button type="primary" @click="submitForm('reportForm')">提交</el-button>
        <el-button @click="resetForm('reportForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { uploadReport, uploadReportAndPic } from '@/api/report'
import { parseTime } from '@/utils/index'
export default {
  data () {
    return {
      reportForm: {
        // 机构基本信息
        companyBasicInfo: {
          companyName: '', // 机构名称
          companyCode: '', // 机构代码
          legalPersonName: '', // 法人
          contactPerson: '', // 联系人
          phone: '', // 手机
          adress: '', // 单位地址
          postcode: '', // 邮编
          companyType: '' // 机构类别
        },
        // 报备信息
        reportInfo: {
          businessCode: '', // 业务统一代码
          projectName: '', // 任务名称
          businessNumber: '', // 业务编号
          businessType: '', // 业务类型
          particiCompany: '', // 受测单位
          entrastCompany: '', // 委托单位
          contactPerson: '', // 联系人
          phone: '' // 联系方式
        },
        // 采样信息
        samplingInfo: {
          monitorProject: '', // 监测项目
          duration: '', // 时间
          samplingPerson: '', // 采样人员
          remake: '' // 备注
        }
      },
      fileList: []
    }
  },
  methods: {
    // 添加文件
    addFile (file, fileList) {
      // 限制文件
      const isIMG = file.raw.type === ('image/jpeg' || 'image/png')
      const isLt10M = file.raw.size / 1024 / 1024 < 10
      if (!isIMG) {
        this.$alert('只能上传图片格式的文件!')
        this.$refs.upload.clearFiles()
        return false
      }
      if (!isLt10M) {
        this.$alert('图片大小不能超过 10MB!')
        this.$refs.upload.clearFiles()
        return false
      }
      this.fileList = fileList
    },
    // 移除文件
    removeFile () {
      this.fileList = []
    },
    // 上传文件
    async uploadFileConfig (val) {
      const fd = new FormData()
      fd.append('uploader', localStorage.getItem('user_name'))
      fd.append('uptime', parseTime(new Date()))
      fd.append('report', JSON.stringify(this.reportForm))
      fd.append('file', val.file)
      const data = await uploadReportAndPic(fd)
      if (data.result.length === 0) {
        this.$message.success('提交成功')
      } else {
        this.$message.error('提交失败')
      }
    },
    // 提交表单
    submitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          // 含图片
          if (this.fileList.length) {
            this.$refs.upload.submit()
          } else { // 不含图片
            const data = {}
            data.uptime = parseTime(new Date())
            data.uploader = localStorage.getItem('user_name')
            data.report = this.reportForm
            const res = await uploadReport(data)
            if (res.result.length === 0) {
              this.$message.success('提交成功')
            } else {
              this.$message.error('提交失败')
            }
          }
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.$refs.upload.clearFiles()
    }
  }
}
</script>

<style lang="less" scoped>
.title {
  padding-top: 10px;
  color: black;
  font-size: 18px;
  text-align: center;
  letter-spacing: 5px;
}
.el-form-item {
  // padding: 0;
  // margin: 0;
  .el-input {
    width: 1000px;
  }
  /deep/ .el-upload-list {
    width: 300px;
  }
}
</style>
