import request from '@/utils/request'
// 上传报告不含图片
export function uploadReport (data) {
  return request({
    url: '/report/uploadform',
    method: 'post',
    data: data
  })
}
// 上传报告包含图片
export function uploadReportAndPic (data) {
  return request({
    url: '/report/uploadformandfile',
    method: 'post',
    data: data
  })
}
// 全部报告
export function getAllReport () {
  return request({
    url: '/report/reportlist',
    method: 'get'
  })
}
// 审批报告
export function approveReport (id, approve) {
  return request({
    url: `/report/approve?id=${id}&approve=${approve}`,
    method: 'get'
  })
}
// 删除删除文件  '/report/delete?id=?'
export function deleteReport (id) {
  return request({
    url: `/report/delete?id=${id}`,
    method: 'get'
  })
}
// 查找报告
export function searchReport (keyword) {
  return request({
    url: `/report/find?keyword=${keyword}`,
    method: 'get'
  })
}
